body {
  margin: 0;
  padding: 0;
}
.wrapper-paper {
  width: 350px;
  height: 90vh;
  margin: 20px auto;
  overflow: hidden;
  position: relative;
}
.tabs {
  width: 100%;
}
.title-grid {
  background: linear-gradient(90deg, #ff3b57, #892168);
}
.title {
  width: 100%;
  height: 40px;
  padding-top: 10px;
  color: white;
}
.play-list {
  width: 100%;
  height: 80%;
  overflow: scroll;
  overflow-x: hidden;
}
.play-list img {
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.music-player-bottom {
  padding: 4px 6px;
  width: 100%;
  height: 20%;
}
.music-player-bottom img {
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.slide {
  position: relative;
  margin-left: 0;
  padding: 20px 0px;
  background: #353839;
  color: white;
  z-index: 10000;
}
/* playing animation */
.anim-div {
  width: 18px;
  height: 20px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin: 0px auto;
  margin-right: 10px;
  transform: scale(1.4);
}
.anim-div .one,
.two,
.three {
  margin: 0px 0.6px;
}
.anim-div .one {
  width: 3px;
  animation: dance1 1.2s linear infinite;
  background: #ff3b57;
}
.anim-div .two {
  width: 3px;
  animation: dance2 1.2s linear infinite;
  background: #ff3b57;
}
.anim-div .three {
  width: 3px;
  animation: dance3 1.2s linear infinite;
  background: #ff3b57;
  animation-delay: 0.3s;
}
@keyframes dance1 {
  0% {
    height: 4px;
  }
  60% {
    height: 11px;
  }
  100% {
    height: 4px;
  }
}
@keyframes dance2 {
  0% {
    height: 12px;
  }
  60% {
    height: 5px;
  }
  100% {
    height: 10px;
  }
}
@keyframes dance3 {
  0% {
    height: 4px;
  }
  50% {
    height: 9px;
  }
  100% {
    height: 3px;
  }
}

@media only screen and (max-width: 500px) {
  .wrapper-paper {
    height: 100vh;
    margin: 0px auto;
  }
}
@media only screen and (max-width: 370px) {
  .wrapper-paper {
    width: 100%;
  }
}
